html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  background-color: #98a6bc;
}

a {
  color: black;
  text-decoration: none;
}

button:focus {
  outline: none;
}

.pokedex-header {
  width: 300px;
  height: 50px;
  border: 1px solid black;
  margin: 20px auto 0;
  display: flex;
}

.pokedex-header__title {
  margin: auto;
}

.pokedex-wrap {
  width: 45%;
  display: inline-block;
  margin: 0 0 20px 5%;
}

.pokedex__pokemons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.pokedex__pokemon {
  background-color: #93a5c1;
  cursor: pointer;
  margin-top: 10px;
  margin-left: 10px;
  display: inline-block;
  width: 200px;
  text-align: center;
  border: 1px solid black;
}

.pokedex__pokemons-image {
  margin-top: 10px;
}

.pokedex__pokemons-name {
  margin-top: 10px;
}

.pokedex__pokemons-name {
  text-transform: capitalize;
}

.pokedex__limit {
  width: 90%;
  margin-top: 10px;
  margin: 10px 0 20px 5%;
  height: 40px;
  border-radius: 10px;
  border: none;
  color: white;
  background-color: #008cff;
}

.pokedex__limit:hover {
  background-color: #239cff;
  cursor: pointer;
}

.pokedex-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 75px;
  position: sticky;
  top: 100px;
  width: 30%;
  height: 500px;
  border: 1px solid black;
  background-color: #93a5c1;
  margin-top: 10px;
}

.pokedex-info__image {
  width: 150px;
  height: 120px;
}

.main__block {
  margin-top: 40px;
  width: 100%;
  display: flex;
  justify-content: flex-start;
}

.pokemon-table {
  border-collapse: collapse;
  width: 90%;
  text-align: center;
}

td, tr {
  border: 1px solid black;
}

.pokedex-info__name {
  font-size: 24px;
  font-weight: 700;
  margin: 20px 0;
}

.pokedex__scroll {
  display: inline-block;
}

.main {
  text-align: center;
}

.label-type {
  position: relative;
  top: 20px;
  font-weight: 500;
  font-size: 18px;
}

.main__selected {
  margin-left: 5px;
  width: 90px;
  text-align: center;
}

.spiner {
  transform: translate(0, -50%)
}

.main-spiner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media all and (max-width: 740px) {
  .main__block {
    justify-content: center;
  }

  .pokedex-info {
    margin-right: 5%;
    width: 40%;
  }

  .pokedex-info__image {
    width: auto;
    height: auto;
  }

  .pokedex__pokemon {
    width: 150px;
  }
}

@media all and (max-width: 500px) {
  .main__block {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
  }

  .pokedex-info {
    margin-right: 0;
    position: static;
    width: 90%;
    margin-left: 0;
  }

  .pokedex-wrap {
    margin: 0;
    width: 90%;
  }

  .pokedex__pokemon {
    width: 100%;
    margin-left: 0;
  }

  .pokedex-header {
    width: 90%;
  }

  .pokedex__limit {
    width: 100%;
    margin-left: 0;
  }
}